      <div id="experience" class="margin-top20px">
        <h2><span class="text-underline">EXP</span>ERIENCE</h2>
        <ul class="margin-top10px">
          <li>
            <p>
              <em class="text-small">National University of Singapore Summer Program, Advisor: <a class="text-underline" href="https://www.comp.nus.edu.sg/~dumitrel/" target="_blank">Prof. Dumitrel</a> </em>
              <em class="text-small float-right">05/2023 - 07/2023.</em>
            </p>
            <h5><span class="text-extra-large font-weight-700">·</span> Project: Online Roam Assistant with Cloud Computing and Big Data</h5>
            <details>
              <summary>
                <em class="text-small">The project was selected as the BEST PROJECT of the course and I got the BEST STUDENT through the project. (...)
                </em>
              </summary>
              <p class="text-small text-indent2">Developed a webapp that helped travelers to arrange their travel plan, including transports, restaurants,
