              <p class="text-small text-indent2">Worked based on Flask, Vue3, Kubernetes, Mysql.</p>

            </details>
          </li>
          <li>
            <p>
              <em class="text-small">Undergraduate Research Assistant, Advisor: <a class="text-underline" href="https://www.sustech.edu.cn/zh/faculties/english-yao-xin.html" target="_blank">Prof. Xin Yao, IEEE Fellow</a></em>
              <em class="text-small float-right">02/2023 - 06/2023.</em>
            </p>
            <h5><span class="text-extra-large font-weight-700">·</span> Project: Dynamic Workflow Schedule in Cloud based
              on Evolutionary Project</h5>
            <details>
              <summary>
                <em class="text-small">Working on a semester project proposed by Prof. Xin Yao in the field of evolution
                  algorithm to find a solution to Dynamic Workflow Scheduling in Cloud. (...)
                </em>
              </summary>
              <p class="text-small text-indent2">In the first stage, evolutionary algorithm is used to solve the Static Multi-objective Workflow Scheduling
                problem in cloud. When the multi-objective conflicts exist, the Pareto-frontier solution set corresponding to
                different constraint tasks is found, so that users can flexibly choose their own workflow scheduling scheme
                according to their own needs.</p>
              <p class="text-small text-indent2">Do a survey on the dynamic factors in dynamic problems, and propose a multi-population repair strategy to
                deal with dynamic environments caused by resource failure, doing many comparisons with other strategies
                (NSGA-II-A, NSGA-II-B and NSGA-II-HM) on five classic scientific workflows (Montage, Inspiral,
                Cybershake, Sipht and Epigenomics). Through experimental comparison, it can be seen that our method is
                significantly superior to the baseline method.</p>
              <p class="text-small text-indent2">Wrote a paper for this project and plan to submit it to the conference of IEEE Symposium Series on
                Computational Intelligence.<a class="text-underline" href="https://docs.qq.com/pdf/DRXNBaXRlZmxyU1VJ" target="_blank">(A Novel Similarity-Based Multi-Population Strategy for Dynamic Workflow Scheduling
                  in Cloud Computing Based on NSGA-II)</a></p>
              <p class="text-small text-indent2">Worked based on Java/Python.</p>

            </details>
          </li>
          <li>
            <p>
              <em class="text-small">Undergraduate Research Assistant, Advisor: <a class="text-underline" href="https://www.sustech.edu.cn/zh/faculties/zhangyu.html" target="_blank">Prof. Yu Zhang</a> </em>
              <em class="text-small float-right">09/2022- 01/2023.</em>
            </p>
            <h5><span class="text-extra-large font-weight-700">·</span> Project: Recommendation System based on Multi-Task Learning Project</h5>
            <details>
              <summary>
                <em class="text-small">The project was selected as the Climbing Plan in Guangdong province, with a time limit of one year. (...)
                </em>
              </summary>
              <p class="text-small text-indent2">Did a survey on different multi-task learning (MTL) models (e.g. MMOE, AITM, PLE...).</p>
              <p class="text-small text-indent2">After testing the performance of MMOE model on Alibaba's open-source dataset Ali-CCP, we noticed the
                uneven distribution of “y” in the dataset. We firstly tried to change the loss function to AUCMLoss. The first
                2 million data in Ali-CCP were used for training. For the composite dataset, we generated a dataset with 100
                features and 12000 data, and changed the correlation from 0.5, 0.75 to 0.9, but found that the improvement of AUC is not significant.</p>
              <p class="text-small text-indent2">Attempt to use multi-objective optimization to model losses of multiple tasks (e.g. MetaBalance). Experiments
                showed that MetaBalance was indeed very useful. After adding MetaBalance into our code, it showed a
                significant improvement on the result, reaching the average level.</p>
              <p class="text-small text-indent2">The next stage we planned to add Prompt into our model, based on the idea of pre-training. Dynamically load
                Prompts for different tasks, and interactively train shared parameters between two tasks, then subsequent
                tasks also load Prompts from previous tasks. Hope to improve the results by at least 1% after introducing this
                hierarchical structure.</p>
              <p class="text-small text-indent2">Worked based on Python, Pytorch, Tensorflow.</p>

            </details>
          </li>

        </ul>
