                deal with dynamic environments caused by resource failure, doing many comparisons with other strategies
                like NN-DNSGA-II, NSGA-II-A, NSGA-II-B and NSGA-II-HM. Through experimental comparison, it can
                be seen that our method is significantly superior to the baseline method and has its own strengths
                compared
                to the NN-DNSGA-II method.</p>
              <p class="text-small text-indent2">Wrote a papper for this project and plan to submit it to the 2023 IEEE Symposium
                Series on Computational
                Intelligence.</p>
              <p class="text-small text-indent2">Worked based on Java/Python.</p>
            </details>
          </li>
          <li>
            <details>
              <summary style="font-size: 13.28px;font-weight: bold;">
                Project: An Online Course Platform. (...)<em class="text-small float-right">09/2022-01/2023.</em>
              </summary>
              <p class="text-small text-indent2 margin-top10px">Created an online video course learning platform to realize course creation, upload and other functions,
