              <p class="text-small text-indent2">The platform included five modules: video, assignment, cheating-prevent, payment and notification. The
                course contained different chapters. The course teacher could set the score of each chapter, and the user
                could see the completion progress and the score of each chapter in real time. Course teachers could
                manage users and see their learning in the background.</p>
              <p class="text-small text-indent2">Worked based on PostgreSQL, Vue3, and SpringBoot2.</p>
            </details>
          </li>
          <li>
            <details>
              <summary style="font-size: 13.28px;font-weight: bold;">
                Project: Ai Agent for Othello Game (aka. Reversi). (...)<em class="text-small float-right">09/2022-10/2022.</em>
              </summary>
              <p class="text-small text-indent2 margin-top10px">Implemented an ai agent by exploring different search algorithms (e.g. Min-max search, Alpha-Belta Pruning
