              <p class="text-small text-indent2">Used evolutionary algorithms to optimize evaluation function (weight of chessboard).</p>
              <p class="text-small text-indent2">Worked based on python3.</p>
            </details>
          </li>
          <li>
            <details>
              <summary style="font-size: 13.28px;font-weight: bold;">
                Project: Single-core CPU Based on Minisys using FPGA Chip. (...)<em class="text-small float-right">02/2022-05/2022.</em>
              </summary>
              <p class="text-small text-indent2 margin-top10px">Developed a single-core CPU covering all instructions in the supported ISA (Minisys) on a FPGA
                development board.</p>
              <p class="text-small text-indent2">Used ‘uart’ interface to load different programs written by assembler language to the CPU for execution
                without re-burning the FPGA chip.
              </p>
              <p class="text-small text-indent2">Worked based on Verilog, C Languages and Minisys.</p>
            </details>
          </li>
          <li>
            <details>
              <summary style="font-size: 13.28px;font-weight: bold;">
                Project: Archaeological Visualization of Sanxingdui. (...)<em class="text-small float-right">02/2022-05/2022.</em>
              </summary>
              <em class="text-small">The full name of this project was Sanxingdui Xiajiao Mountain Sacrifice Scene Visualization, which was proposed by
                Shenzhen Museum and cooperated with Professor Tang Jigen's research group and Professor Liu Jiang, aiming to use
                computer visualization, modeling, animation and other technologies to integrate with Chinese culture and restore
                Sanxingdui Sacrifice Scene as much as possible.
              </em>
              <p class="text-small text-indent2 margin-top10px">The model was simplified using the quadratic error measure (QEM) simplification method, and then the model
                was dynamically rendered with openGL to show the animation effect.
              </p>
              <p class="text-small text-indent2">Used blender and unity to model the Sanxingdui sacrificial scene. In addition, text-to-speech (TTS) recognition
                technology was also used to enable users to view the model from the first perspective and conduct voice
                interpretation.
              </p>
              <p class="text-small text-indent2">Worked based on C#, Unity, Blender.</p>
            </details>
